@import "src/common/styles/mixins/mixins";
@import "src/common/styles/variables/variables";


.wrapper_tile {
    display: flex;
    flex-wrap: wrap;
    gap: 21px;

    @media (max-width: 768px) {
        gap: 35px;

    }
    @media (max-width: 320px) {
        gap: 8px;

    }

    .bookList__item {
        width: 190px;
        height: 470px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        gap: 16px;
        padding: 8px 8px 16px;
        box-shadow: 0px 2px 4px rgba(191, 196, 201, 0.2), 0px 3px 4px rgba(191, 196, 201, 0.18), 0px 1px 5px rgba(191, 196, 201, 0.24);
        border-radius: 10px;

        @media (max-width: 768px) {
            gap: 0;
        }

        @media (max-width: 320px) {
            width: 288px;
            height: 470px;
            padding: 8px 16px 16px 16px;
            align-items: center;
        }

        .bookList__item_coveWrapper {
            display: flex;
            align-items: center;
            justify-content: center;


            @media (max-width: 768px) {
                margin-bottom: 16px;
                gap: 0;
            }

            .bookList__item_cover {
                border-radius: 10px;
                border: 1px solid $unActive-color;
                box-sizing: border-box;
                width: 174px;
                height: 242px;
                background-size: cover;
            }
        }

        .bookList__item_rating {

            @media (max-width: 768px) {
                margin-bottom: 25px;
            }

            @media (max-width: 320px) {
                margin-bottom: 16px;
            }
        }

        .bookList__item_info {
            width: 174px;
            height: 90px;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            gap: 8px;

            @media (max-width: 768px) {
                gap: 17px;
                margin-bottom: 28px;
                height: 70px;
                justify-content: flex-start;
            }

            @media (max-width: 320px) {
                width: 256px;
                gap: 8px;
                height: 98px;
                margin-bottom: 10px;
                justify-content: flex-start;
            }

            .bookList__item_info_title {
                display: flex;
                letter-spacing: 0.1px;
                height: 54px;
                align-items: center;
                @include text(14px, 18px, 600, $main-text-color);

                @media (max-width: 768px) {
                    @include text(14px, 18px, 700, $main-text-color);

                    align-items: flex-start;
                    overflow: hidden;
                    display: -webkit-box;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                    line-height: 1.3em;
                    max-height: 3.9em;
                }

                @media (max-width: 320px) {
                    @include text(14px, 18px, 600, $main-text-color);
                    align-items: flex-start;
                    max-height: 36px;
                    justify-content: flex-start;
                    display: -webkit-box;
                    -webkit-line-clamp: 3;
                    -webkit-box-orient: vertical;
                    line-height: 1.3em;
                    height: 3.9em;
                }
            }

            .bookList__item_info_author {
                height: 36px;
                width: 174px;
                letter-spacing: 0.1px;
                @include text(14px, 18px, 400, $unActive-color);

                @media (max-width: 768px) {
                    @include text(12px, 18px, 700, #727272);
                    text-transform: uppercase;
                }

                @media (max-width: 320px) {
                    @include text(12px, 18px, 400, #727272);
                    text-transform: capitalize;
                }
            }
        }

        .bookList__item_button {
            width: 166px;
            height: 40px;
            align-self: center;

            @media (max-width: 768px) {
                width: 166px;
                height: 40px;
                align-self: center;
            }

            @media (max-width: 320px) {
                width: 256px;
                height: 40px;
                align-self: center;
            }

        }
    }
}
